<template>
  <v-container class="fill-height">
    <v-sheet
      elevation="2"
      class="mx-auto d-flex fill-height flex-grow-1 flex-column rounded-sm"
      max-width="600"
    >
      <div>
        <v-text-field
          class="px-4 py-2"
          solo
          v-model="searchString"
          prepend-inner-icon="mdi-magnify"
          :label="$t('transport.searchRoute')"
          required
          clearable
          hide-details="auto"
        ></v-text-field>
      </div>

      <div class="flex-grow-1 py-2" ref="scrollBoxContainer">
        <v-virtual-scroll
          :items="kmbRoutesResultQuery"
          :item-height="90"
          :height="scrollBoxHeight"
        >
          <template v-slot:default="{ item }">
            <RouteItem @onClick="clickRouteItem" :routeItem="item" />
          </template>
        </v-virtual-scroll>
      </div>
    </v-sheet>
  </v-container>
</template>

<script>
import { actionTypes } from "@/store/store-types";
import { mapGetters } from "vuex";
import RouteItem from "@/components/RouteItem.vue";
export default {
  components: {
    RouteItem,
  },
  data() {
    return {
      searchString: "",
      scrollBoxHeight: 0,
    };
  },
  computed: {
    ...mapGetters(["kmbRoutesData"]),
    kmbRoutesResultQuery() {
      return this.kmbRoutesData.filter((item) =>
        item.route.toLowerCase().includes(this.searchString.toLowerCase())
      );
    },
  },
  mounted() {
    this.checkData();
    this.scrollBoxHeight = this.$refs.scrollBoxContainer.clientHeight - 18;
  },
  methods: {
    async checkData() {
      if (this.kmbRoutesData.length === 0) {
        console.log("load kmbRoutesData data");
        await this.$store.dispatch(actionTypes.fetchKmbRoutes);
        console.log("finish kmbRoutesData data");
      }
    },
    clickRouteItem(item) {
      const { route, bound } = item;
      const serviceType = item.service_type;
      console.log(item);
      this.$router.push({
        name: "routeStop",
        query: {
          route,
          bound,
          serviceType,
        },
      });
    },
  },
};
</script>

